import React from "react"
import Component from "./Component"

const Container: React.FC = () => {
  const tabs = React.useRef({
    balBikash: {
      id: "bal-bikash",
      label: "What is Bal Bikash"
    },
    objective: {
      id: "objective",
      label: "Objective"
    },
    organization: {
      id: "organization",
      label: "Organization Charter"
    }
  })

  const [currentView, setCurrentView] = React.useState("balBikash")

  const setView = React.useCallback(
    (id: string) => {
      setCurrentView(id)
    },
    [currentView, setCurrentView]
  )

  return (
    <Component
      tabConfig={tabs.current}
      currentView={currentView}
      setView={setView}
    />
  )
}

export default Container
